/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-cjp-blocks-masonry {
	img {
		max-width: 100%;
		height: auto;
	}
}

.masonry-container {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 1rem;

	.col {
		display: flex;
		flex-direction: column;
		gap: 1rem;
	}
}

.masonry-container.col-2 {
	grid-template-columns: repeat(2, 1fr);
}

.masonry-container.col-4 {
	grid-template-columns: repeat(4, 1fr);
}

@media (max-width: 980px) {
	.masonry-container {
		grid-template-columns: repeat(2, 1fr) !important;
	}
}

@media (max-width: 640px) {
	.masonry-container {
		gap: 0.5rem;

		.col {
			gap: 0.5rem;
		}
	}
}

.masonry-order-list {
	margin: 0;
	padding: 0;
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	gap: 1rem;

	li:hover {
		cursor: grab;
	}

	li:active {
		cursor: grabbing;
	}

	li {
		margin: 0;
		padding: 0;
		list-style: none;
		display: flex;
		gap: 1rem;
		border-bottom: 1px solid #e0e0e0;
	}

	img {
		width: 50%;
	}
}
